import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { postSignup } from "../../api/signup";
import {
  ContainerDiv,
  FlexForm,
  InputContent,
  ButtonStyle,
  BorderDiv,
  Background,
  IconContainer,
} from "./styles";
import { useMutation, useQueryClient } from "react-query";
import { colors } from "../../color/colors";
import { SignupIcon } from "../../icon/icons";

function SignUp() {
  const navigate = useNavigate();
  const [nickname, setNickname] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [checkPassword, setCheckPassword] = useState("");

  const nickNameOnChangeHandler = (e) => {
    setNickname(e.target.value);
  };
  const emailOnChangeHandler = (e) => {
    setEmail(e.target.value);
  };
  const pwOnChangeHandler = (e) => {
    setPassword(e.target.value);
  };
  const setPwOnChangeHandler = (e) => {
    setCheckPassword(e.target.value);
  };
  const onSubmitHandler = async (e) => {
    if (!nickname || !email || !password) {
      alert(", ,   ");
      return;
    }

    if (password !== checkPassword) {
      alert("    ");
      return;
    }

    if (!email.includes("@")) {
      alert("  .");
      return;
    }
    mutation.mutate({ nickname, email, password });
  };

  const queryClient = useQueryClient();
  const mutation = useMutation(postSignup, {
    onSuccess: () => {
      queryClient.invalidateQueries("signup");
      console.log("mutation .");
      navigate("/login");
    },
    onError: () => {
      queryClient.invalidateQueries("signup");
      console.log("mutation .");
    },
  });
  // mutation(=)  //   
  // onSucess mutation     

  return (
    <>
      <Background back-color={colors.background}>
        <ContainerDiv>
          <FlexForm>
            <IconContainer>
              <SignupIcon />
            </IconContainer>
            <BorderDiv>
              <InputContent
                type="text"
                placeholder=""
                value={nickname}
                onChange={nickNameOnChangeHandler}
              />
              <InputContent
                type="text"
                placeholder=""
                value={email}
                onChange={emailOnChangeHandler}
              />
              <InputContent
                type="password"
                placeholder=""
                value={password}
                onChange={pwOnChangeHandler}
              />
              <InputContent
                type="password"
                placeholder=" "
                value={checkPassword}
                onChange={setPwOnChangeHandler}
              />
              <p>
                <ButtonStyle
                  onClick={onSubmitHandler}
                  font-color={"white"}
                  back-color={colors.darkColor}
                  type="submit"
                >
                   
                </ButtonStyle>
              </p>
              <ButtonStyle
                onClick={() => {
                  navigate("/login");
                }}
                font-color={"black"}
                back-color={"transparent"}
              >
                 
              </ButtonStyle>
            </BorderDiv>
          </FlexForm>
        </ContainerDiv>
      </Background>
    </>
  );
}

export default SignUp;
