import React, { useState } from "react";
import {
  CommentContainer,
  CommentInput,
  CommentP,
  CommentButtons,
  CommentButton,
} from "./commentStyles";

import { getCookie } from "../../../cookies/cookies";
import base64 from "base-64";


function Comment({ comment, onEdit, onDelete, memberId, commentId }) {

  const [isEditMode, setIsEditMode] = useState(false);
  const [editedComment, setEditedComment] = useState(comment);

  //  
  const token = getCookie("token");
  let dec = "";
  if (token) {
    const payload = token.substring(
      token.indexOf(".") + 1,
      token.lastIndexOf(".")
    );
    dec = JSON.parse(base64.decode(payload));
  }

  // -----------------------------------------------------------------------------

  // input  
  const handleEditClick = () => {
    setIsEditMode(true);
  };

  const handleSaveClick = () => {
    onEdit(editedComment);
    setIsEditMode(false);
  };

  const handleCancelClick = () => {
    setEditedComment(comment);
    setIsEditMode(false);
  };
  // -----------------------------------------------------------------------------

  return (
    <CommentContainer>
      {dec.member_id === memberId ? (
        isEditMode ? (
          <>
            <CommentInput
              type="text"
              value={editedComment}
              onChange={(e) => setEditedComment(e.target.value)}
            />
            <CommentButtons>
              <CommentButton onClick={handleSaveClick}></CommentButton>
              <CommentButton onClick={handleCancelClick}></CommentButton>
            </CommentButtons>
          </>
        ) : (
          <>
            <CommentP>: {comment}</CommentP>
            <CommentButtons>
              <CommentButton onClick={handleEditClick}></CommentButton>
              <CommentButton onClick={onDelete}></CommentButton>
            </CommentButtons>
          </>
        )
      ) : (
        <>
          <CommentP>: {comment}</CommentP>
          <CommentButtons>{/*     */}</CommentButtons>
        </>
      )}
    </CommentContainer>
  );
}

export default Comment;
